export default "1,100 കിലോമീറ്റർ കന്യാകുമാരി-മുംബൈ ഇടനാഴി - <b>65,000 കോടി</b><br>ഇതിന്റെ 600 കിലോമീറ്റർ കേരളത്തിലൂടെ കടന്നുപോകുന്നു.<br><br>കൊച്ചി മെട്രോ രണ്ടാം ഘട്ടം - <b>1957 കോടി</b><br>കക്കനാടുമായി ബന്ധിപ്പിക്കും. കലൂർ സ്റ്റേഡിയം മുതൽ ഇൻഫോപാർക്ക് വരെ 11.5 കിലോമീറ്റർ, 11 സ്റ്റേഷനുകൾ<br><br><a href=\https://www.aninews.in/news/national/general-news/union-budget-kerala-gets-rs-65000-crore-for-nh-works-rs-1957-cr-for-kochi-metro20210201182124/\>https://www.aninews.in/news/national/general-news/union-budget-kerala-gets-rs-65000-crore-for-nh-works-rs-1957-cr-for-kochi-metro20210201182124/</a>"
